<template><section><html><head></head><body><h1>VcDoubleCircleRipple</h1>
<p>The <code v-pre="">vc-ripple-circle-double</code> component is used to load the double circle ripple effect. Refer to <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a>.</p>
<h2>Example</h2>
<h3>Load a VcDoubleCircleRipple</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-ripple-circle-double @ready="subReady" image-url="./statics/SampleData/images/redCircle2.png" :position="position" ref="circle"></vc-ripple-circle-double>
        <vc-ripple-circle-double @ready="subReady" image-url="./statics/SampleData/images/redCircle2.png" :position="position" ref="circle" :height="3000"></vc-ripple-circle-double>
        <vc-entity>
          <vc-graphics-polyline :positions="positions" :width="5" :material="material"></vc-graphics-polyline>
        </vc-entity>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu map-style="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
