<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre="">required</code> Specify the location where the double circle ripple effect entity is added. structure: { lng: number, lat: number, height: number } or Cesium.Cartesian3</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Specifies whether the double circle ripple effect is visible.</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre="">undefined</code></td>
<td><code v-pre="">optional</code> Specify the height of the double circle ripple effect ellipse. Unit: Meter.</td>
</tr>
<tr>
<td>minRadius</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> Specify the minimum radius of the double circle ripple effect. Unit: Meter.</td>
</tr>
<tr>
<td>maxRadius</td>
<td>Number</td>
<td><code v-pre="">3000</code></td>
<td><code v-pre="">optional</code> Specify the maximum radius of the double circle ripple effect. Unit: Meter.</td>
</tr>
<tr>
<td>deviationRadius</td>
<td>Number</td>
<td><code v-pre="">20</code></td>
<td><code v-pre="">optional</code> Specify the difference between the radius of the double circle. The larger the value, the faster the speed.</td>
</tr>
<tr>
<td>interval</td>
<td>Number</td>
<td><code v-pre="">3000</code></td>
<td><code v-pre="">optional</code> The time interval between two circles, in milliseconds.</td>
</tr>
<tr>
<td>imageUrl</td>
<td>String</td>
<td><code v-pre="">''</code></td>
<td><code v-pre="">optional</code> Specify the picture used to express the double-circle ripple effect.</td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre="">'white'</code></td>
<td><code v-pre="">optional</code> Specify the color of the double circle ripple effect.</td>
</tr>
</tbody>
</table>
<hr>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and an Entity array.</td>
</tr>
</tbody>
</table>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          position: { lng: 117.217124, lat: 31.809777 },
          positions: [{ lng: 117.217124, lat: 31.809777, height: 0 }, { lng: 117.217124, lat: 31.809777, height: 3000 }],
          material: {
            fabric: {
              type: 'PolylineGlow',
              uniforms: { color: 'red', glowPower: 0.5 }
            }
          }
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
        },
        subReady() {
          const { Cesium, viewer } = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          viewer.zoomTo(viewer.entities)
        }
      }
    }
  </script>